  margin: auto;
  background-color: #0D0D0D;
  color: #FFFCF9;
  font-family: "Manrope", sans-serif;
  font-size: 24px;
}

.container {
  margin: 0 auto;
  max-width: 1074px;
  width: 74.5833333333%;
}

section {
  position: relative;
}

h1,
h2 {
  font-family: "Migra", sans-serif;
  font-weight: 800;
