}

.main {
  padding: 80px 0;
}

.switcher {
  display: inline-block;
  height: 18px;
  position: relative;
  width: 36px;
}
.switcher input {
  display: none;
}
.switcher input:checked + .switcher__slider {
  background-color: #7FFBB4;
}
.switcher input:checked + .switcher__slider::before {
  left: calc(100% - 16px - 1px);
  background-color: #08833B;
}
.switcher__slider {
  background-color: #E8E8E8;
  position: absolute;
  inset: 0;
  transition: 0.3s;
  cursor: pointer;
  border-radius: 2rem;
}
.switcher__slider::before {
  content: "";
  background-color: #4E4E4E;
  bottom: 1px;
  left: 1px;
  height: 16px;
  position: absolute;
  transition: 0.3s;
  width: 16px;
  border-radius: 50%;
}

.what-nft__container {
  width: 69.0277777778%;
  max-width: 994px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.what-nft__title {
  margin-bottom: 40px;
}
.what-nft__grid {
  margin-top: 80px;
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.what-nft__grid-item {
  padding: 72px 24px 72px 120px;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.what-nft__grid-item::after {
  content: "";
  background: url("../images/what-nft_1.jpg") no-repeat bottom right/cover;
  position: absolute;
  inset: 0;
  z-index: -1;
  pointer-events: none;
}
.what-nft__grid-item::before {
  content: "";
  position: absolute;
  inset: 0;
  background: #0D0D0D;
  opacity: 0.7;
  pointer-events: none;
  z-index: 0;
}
.what-nft__grid-item:nth-child(2) {
  padding-left: 24px;
  padding-right: 120px;
}
.what-nft__grid-item:nth-child(2)::after {
  background: url("../images/what-nft_2.jpg") no-repeat bottom right/cover;
}
.what-nft__item-list {
  margin-top: 35px;
  position: relative;
  z-index: 1;
}
.what-nft__item-title {
  width: 70%;
  position: relative;
  z-index: 1;
}

.nft {
  padding-top: 80px;
}
.nft__tab {
  padding: 40px;
  background-color: #0D0D0D;
  border-radius: 20px;
  transition: 0.3s ease-in-out all;
  box-shadow: inset 0 0 20px 0 rgba(255, 255, 255, 0.3);
}
.nft__tab.active {
  box-shadow: none;
  filter: drop-shadow(0px 0 15px rgba(255, 255, 255, 0.5));
}
.nft__tab:not(:last-child) {
  margin-bottom: 80px;
}
.nft__tab_gold {
  box-shadow: inset 0 0 20px 0 rgba(255, 206, 0, 0.3);
}
.nft__tab_gold .nft__card {
  box-shadow: 0 0 24px rgba(255, 206, 0, 0.4);
}
.nft__tab_gold.active {
  filter: drop-shadow(0px 0 15px rgba(255, 206, 0, 0.5));
}
.nft__tab-top-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
}
.nft__tab-top-left {
  flex: 1;
}
.nft__tab-subtitle {
  margin-top: 3px;
}
.nft__active-cards {
  margin-top: 20px;
  display: flex;
  align-items: center;
  gap: 10px;
}
.nft__show-hide {
  font-size: 12px;
}
.nft__tab-inner {
  max-height: 0;
  overflow: hidden;
  transition: 0.7s ease-in-out all;
  margin: 0 -40px;
  padding: 0 40px;
}
.nft__tab-pros {
  margin-top: 80px;
}
.nft__card-list {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 5.5555555556vw 2.7777777778vw;
  margin-top: 80px;
  margin-bottom: 40px;
}
.nft__card {
  box-shadow: 0 0 24px rgba(229, 229, 229, 0.4);
  background-color: #0D0D0D;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
  transition: 0.3s ease-in-out all;
}
.nft__card.sold {
  box-shadow: none;
  pointer-events: none;
}
.nft__card.sold .nft__card-image {
  filter: grayscale(1);
}
.nft__card.sold .nft__card-inner {
  background-color: #4E4E4E;
}
.nft__card.sold .nft__card-prive {
  color: #0D0D0D;
}
.nft__card.sold .nft__card-btn {
  background-color: #E8E8E8;
  color: #0D0D0D;
}
.nft__card.hide {
  display: none;
}
.nft__card-image {
  width: 100%;
  position: relative;
  overflow: hidden;
  mask-image: radial-gradient(white, black);
}
.nft__card-image::before {
  content: "";
  display: block;
  padding-bottom: max(min(141.9161676647%, 9999px), 0px);
}
.nft__card-image img {
  width: 100%;
  height: 100%;
  position: absolute;
  object-fit: cover;
  top: 0;
  left: 0;
}
.nft__card-inner {
  width: 100%;
  padding: 12px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.nft__card-btn {
  margin-top: 12px;
}

.btn {
  font-size: 12px;
  font-weight: 500;
  background-color: #7A0901;
  max-width: 124px;
  width: 100%;
  padding: 6px;
  border-radius: 50px;
  text-align: center;
  line-height: 1.5;
  cursor: pointer;
  font-family: "Manrope", sans-serif;
  color: #FFFFFF;
  border: none;
}
.btn_big {
  font-size: 24px;
  max-width: 246px;
  font-weight: 700;
  padding: 12px 10px;
}
.btn_green {
  background-color: #08833B;
}

.btn-plus {
  width: 27px;
  height: 27px;
  transition: 0.3s ease-in-out all;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  cursor: pointer;
}
.btn-plus::before, .btn-plus::after {
  content: "";
  position: absolute;
  height: 4px;
  width: 100%;
  background-color: #F4C500;
  transition: inherit;
}
.btn-plus::after {
  transform: rotate(90deg);
}
.btn-plus.active::before {
  transform: rotate(-45deg);
  background-color: #FFFCF9;
}
.btn-plus.active::after {
  transform: rotate(45deg);
  background-color: #FFFCF9;
}

.btn-close {
  width: 20px;
  height: 20px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.btn-close::before, .btn-close::after {
  content: "";
  position: absolute;
  height: 3px;
  width: 100%;
  background-color: #4E4E4E;
  transition: inherit;
}
.btn-close::after {
  transform: rotate(45deg);
}
.btn-close::before {
  transform: rotate(-45deg);
}

.popup {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  inset: 0;
  z-index: 300;
  opacity: 0;
  visibility: hidden;
}
.popup.active {
  transition: 0.5s ease-in-out all;
  opacity: 1;
  visibility: visible;
}
.popup__bg {
  position: absolute;
  inset: 0;
  z-index: 301;
  background-color: #0D0D0D;
  opacity: 0.4;
  cursor: pointer;
}
.popup__btn-close {
  position: absolute;
  right: 20px;
  top: 20px;
}
.popup__window {
  position: absolute;
  z-index: 302;
  background-color: #E8E8E8;
  border-radius: 20px;
  max-height: 90vh;
  width: 54.5833333333vw;
  height: max-content;
  padding: 80px 40px;
  color: #0D0D0D;
  overflow-y: auto;
  scrollbar-width: thin;
  scrollbar-color: #7A0901 transparent;
}
.popup__window::-webkit-scrollbar {
  width: 4px;
  height: 4px;
}
.popup__window::-webkit-scrollbar-track {
  background: transparent;
}
.popup__window::-webkit-scrollbar-thumb {
  background-color: #7A0901;
  border-radius: 4px;
}
.popup__window::-webkit-scrollbar-thumb:hover {
  background: #555;
}
.popup__inner {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.popup__inner .popup__descr {
  margin-top: 70px;
}
.popup__inner .popup__btn {
  margin-top: 80px;
}
.popup__inner.hide {
  display: none;
}
.popup__form {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.popup__form .popup__descr {
  margin-top: 40px;
}
.popup__form .popup__btn {
  margin-top: 60px;
}
.popup__form.hide {
  display: none;
}
.popup__descr {
  text-align: center;
}
.popup__descr span {
  font-weight: 700;
}
.popup__inputs-list {
  margin-top: 60px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding: 0 60px;
  width: 100%;
}
.popup__input {
  padding: 25px 20px;
  font-family: "Manrope", sans-serif;
  font-size: 24px;
  color: #0D0D0D;
  background-color: #FFFFFF;
  box-shadow: inset 2px 2px 4px rgba(0, 0, 0, 0.08);
  border-radius: 10px;
  line-height: 1.25;
  width: 100%;
}
.popup__input::placeholder {
  color: rgba(13, 13, 13, 0.6);
}
.popup__input-area {
  position: relative;
}
.popup__error {
  position: absolute;
  bottom: 100%;
  color: red;
  font-size: 14px;
}
.popup__error:empty {
  display: none;
}
.popup__images {
  margin-top: 32px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 40px;
}
.popup__images img {
  height: 63px;
  width: 63px;
  object-fit: contain;
}

