    height: 12px;
    width: 12px;
  }
  .what-nft__grid {
    grid-template-columns: 1fr;
    margin-top: 60px;
  }
  .what-nft__grid-item {
    padding: 40px 20px;
  }
  .what-nft__grid-item::after {
    transform: scaleX(-1);
    background-position: bottom center;
  }
  .what-nft__grid-item:nth-child(2) {
    padding: 40px 20px;
  }
  .what-nft__grid-item:nth-child(2)::after {
    transform: scaleX(1);
    background-position: bottom center;
  }
  .what-nft__item-list {
    margin-top: 20px;
  }
  .what-nft__item-title {
    width: 75%;
  }
  .nft {
    padding-top: 60px;
  }
  .nft__tab:not(:last-child) {
    margin-bottom: 60px;
  }
  .nft__tab-pros {
    margin-top: 60px;
  }
  .nft__card-list {
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 25px 20px;
    margin-top: 60px;
  }
  .popup__window {
    padding: 30px;
  }
  .popup__form .popup__btn {
    margin-top: 30px;
  }
  .popup__inner .popup__btn {
    margin-top: 30px;
  }
  .popup__inputs-list {
    margin-top: 30px;
    grid-gap: 15px;
  }
  .popup__input {
    padding: 10px 8px;
