@media only screen and (max-width: 551px) {
  .nft__tab {
    padding: 20px 12px 15px;
  }
  .nft__tab-top-content {
    flex-direction: column;
    gap: 16px;
  }
  .nft__tab-top-left {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .nft__tab-subtitle {
    margin-top: 10px;
  }
  .nft__tab-inner {
    margin: 0 -12px;
    padding: 0 12px;
  }
  .nft__tab-pros {
    margin-top: 25px;
  }
  .nft__card-list {
    grid-template-columns: repeat(2, 1fr);
    margin: 25px -12px 10px;
  }
  .btn-plus::before, .btn-plus::after {
    background-color: #FFFCF9;
    width: 62%;
    height: 3px;
  }
  .btn-plus::before {
    transform: rotate(45deg);
    left: 0;
  }
  .btn-plus::after {
    transform: rotate(-45deg);
    right: 0;
  }
}